//
.svg__icon {
    display: inline-block;
    width: 50px;
    height: 50px;
    stroke-width: 0;
    stroke: currentColor;
    fill: currentColor;
}

.svg__icon--color {
    fill: #15638e;
}

.svg__icon--hover:hover {
    fill: $blue;
}

.svg__icon--cross {
    fill: $blue;
}

